.header {
  background-color: #191919;
  padding-top: 30px;
  padding-bottom: 30px;
  position: relative;
}

.header.active {
  z-index: 10;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.header__container {
  justify-content: space-between;
  align-items: center;
  display: flex;
  position: relative;
}

.header__mixer {
  flex-grow: 1;
  justify-content: space-between;
  display: flex;
}

.logo__img {
  max-width: 100px;
}

.nav {
  display: none;
}

.nav.open {
  animation: showMenu .5s;
  position: absolute;
  top: 52px;
}

.nav__menu {
  flex-direction: column;
  gap: 24px;
  display: flex;
}

.menu__link {
  color: rgba(255, 255, 255, .8);
  font-size: 14px;
  font-weight: 400;
  line-height: 1.25;
  transition: color .3s;
}

@media (hover: hover) {
  .menu__link:hover {
    color: #df3d3a;
  }
}

@media (hover: none) {
  .menu__link:active {
    color: #df3d3a;
  }
}

.open {
  display: flex;
}

.no-scroll {
  overflow: hidden;
}

.burger {
  width: 22px;
  height: 12px;
  background-color: #191919;
  padding: 0;
  position: relative;
}

.burger.active {
  width: 18px;
  height: 18px;
}

.burger .burger__stick {
  height: 2px;
  width: 22px;
  background-color: rgba(255, 255, 255, .8);
  transition: transform .4s;
  display: block;
}

.burger.active .burger__stick {
  transform: scale(0);
}

.burger:before, .burger:after {
  content: "";
  width: 100%;
  height: 2px;
  background-color: rgba(255, 255, 255, .8);
  transition: transform .4s;
  display: block;
  position: absolute;
}

.burger:before {
  top: 0;
}

.burger:after {
  bottom: 0;
}

.burger.active:before {
  border-radius: 10px;
  top: 50%;
  transform: rotate(45deg)translateY(-50%);
}

.burger.active:after {
  border-radius: 10px;
  bottom: 50%;
  transform: rotate(-45deg)translateY(50%);
}

@media (min-width: 768px) {
  .header {
    padding-top: 50px;
    padding-bottom: 50px;
  }

  .header.active {
    inset: inherit;
    padding-top: 50px;
    padding-bottom: 100px;
    animation: showTabletMenu .5s;
    position: absolute;
    left: 0;
    right: 0;
    box-shadow: 0 20px 80px #000;
  }

  .header__container {
    flex-direction: column;
    align-items: stretch;
    gap: 52px;
    padding-left: 60px;
    padding-right: 60px;
  }

  .nav.open {
    top: inherit;
    position: relative;
  }

  .menu__link {
    font-size: 16px;
  }
}

@media (min-width: 1200px) {
  .header {
    padding-top: 65px;
    padding-bottom: 70px;
  }

  .header__container {
    justify-content: space-between;
    align-items: center;
    gap: inherit;
    flex-direction: row;
  }

  .header__mixer {
    flex-grow: inherit;
  }

  .logo {
    width: 130px;
  }

  .nav {
    display: block;
  }

  .nav__menu {
    flex-direction: row;
    gap: 75px;
  }

  .burger {
    display: none;
  }
}

@keyframes showMenu {
  from {
    opacity: 0;
    display: none;
    transform: translateX(-100%);
  }

  60% {
    opacity: 1;
  }

  to {
    display: flex;
    transform: translateX(0);
  }
}

@keyframes showTabletMenu {
  from {
    padding-top: 50px;
    padding-bottom: 50px;
  }

  to {
    padding-top: 50px;
    padding-bottom: 100px;
  }
}

